import React, { useEffect, useRef, useState } from 'react';
import Home from './Pages/Home/Home';
import Login from './Pages/Login/Login';
import { Routes, Route, useNavigate } from "react-router-dom";
import { useAppDispatch, useAppSelector } from './components/Redux/hooks';
import { useFirebase } from './components/hooks/useFirebase';
import { gellAllGroups, getAllUsers } from './components/Redux/counterSlice';
import PrivateRoute from './components/PrivateRoute/PrivateRoute';
import UserSetting from './Pages/UserSetting/UserSetting';
import io from 'socket.io-client';
import { ForgetPass } from './components/ForgetPass/ForgetPass';


const App = () => {

  const { loginUser } = useAppSelector(state => state?.data);
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const socketClient = useRef<SocketIOClient.Socket>();



  // https://enigmatic-garden-12643.herokuapp.com/
  useEffect(() => {
    // socketClient.current = io.connect("http://localhost:5000");
    dispatch(gellAllGroups());
    dispatch(getAllUsers());
  }, [loginUser]);

  // // loginUser
  // useEffect(() => {
  //   if (loginUser.uid === null) return;
  //   socketClient?.current?.emit('login', { loginUser });
  // }, [loginUser, socketClient?.current]);

  return (

    <>

      <Routes>

        <Route path="/" element={<PrivateRoute>
          <Home />
        </PrivateRoute>} />

        <Route path="/login" element={<Login />}
        />

        <Route path="/reseatpass" element={<ForgetPass />}
        />

        <Route path="/setting/:userId" element={<UserSetting />}
        />

      </Routes>
      {/* <Login /> */}

    </>
  );
}

export default App;
